import React, { useState } from "react"
import { useStaticQuery, graphql } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import MenuItem from "../../components/menuitem.js"

import { Icon, Row, Col } from "react-materialize"

const Skewers_Menu_Page = () => {
  const images = useStaticQuery(graphql`
    query {
      gyoza: file(relativePath: { eq: "rice/gyoza.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      fried_rice: file(relativePath: { eq: "rice/fried_rice.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 1000, maxHeight: 650) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  const [numTotalItems, setItems] = useState(0)

  const addItem = () => {
    setItems(numTotalItems + 1)
  }

  const removeItem = () => {
    setItems(numTotalItems - 1)
  }

  return (
    <Layout>
      <SEO title="Rice | Online Ordering" />
      <div style={{ marginTop: "4%", marginLeft: "10%", width: "80%" }}>
        <Row>
          <Col s={4}>
            <MenuItem
              key={"1"}
              title="Gyoza"
              price={6}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.gyoza.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"2"}
              title="Fried Rice"
              price={12}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.fried_rice.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"3"}
              title="Curry & Rice"
              price={12}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.gyoza.childImageSharp.fluid}
            />
          </Col>
          <Col s={4}>
            <MenuItem
              key={"4"}
              title="Cooked Mushroom Rice"
              price={8}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.gyoza.childImageSharp.fluid}
            />
          </Col>
        </Row>
      </div>
    </Layout>
  )
}

export default Skewers_Menu_Page
